import React from 'react'
import { Container } from './styles'
import { useState } from 'react'
import { Link } from 'react-scroll'
import LanguageSwitch from '../LanguageSwitch/LanguageSwitch'
import { useTranslation } from 'gatsby-plugin-react-i18next'

export function Header() {
  const [isActive, setActive] = useState(false)
  const { t } = useTranslation()

  function toggleTheme() {
    let html = document.getElementsByTagName('html')[0]
    html.classList.toggle('light')
  }

  function closeMenu() {
    setActive(false)
  }

  return (
    <Container className="header-fixed">
      <a className="logo" href="/#home">
        <span>F</span>
        <span>M</span>
      </a>
      <input
        onChange={toggleTheme}
        className="container_toggle"
        type="checkbox"
        id="switch"
        name="mode"
      />
      <label htmlFor="switch">Toggle</label>
      <LanguageSwitch />
      <nav className={isActive ? 'active' : ''}>
        <Link
          activeClass="active"
          to="home"
          spy={true}
          smooth={true}
          offset={-70}
          duration={500}
        >
          {t('menu_home')}
        </Link>

        <Link
          activeClass="active"
          to="skills"
          spy={true}
          smooth={true}
          offset={-70}
          duration={500}
        >
          {t('menu_skills')}
        </Link>

        <Link
          activeClass="active"
          to="certificates"
          spy={true}
          smooth={true}
          offset={-70}
          duration={500}
        >
          {t('menu_certificates')}
        </Link>

        <Link
          activeClass="active"
          to="experiences"
          spy={true}
          smooth={true}
          offset={-70}
          duration={500}
        >
          {t('menu_experience')}
        </Link>

        <Link
          activeClass="active"
          to="projects"
          spy={true}
          smooth={true}
          offset={-70}
          duration={500}
        >
          {t('menu_projects')}
        </Link>
        <a href="/assets/fadlicv.pdf" download="" className="button">
          CV
        </a>
      </nav>

      <div
        aria-expanded={isActive ? 'true' : 'false'}
        aria-haspopup="true"
        className={isActive ? 'menu active' : 'menu'}
        onClick={() => {
          setActive(!isActive)
        }}
      ></div>
     
    </Container>
  )
}
