import React from 'react'
import { Container } from './styles'
// import githubIcon from "../../assets/github-icon.svg"
// import externalLinkIcon from "../../assets/external-link-icon.svg"
import ScrollAnimation from 'react-animate-on-scroll'
import { lists } from '../../config/certificates'
import { convertToDate } from '../../helpers/time'
import { useTranslation } from 'gatsby-plugin-react-i18next'

export function Certificates() {
  const { t } = useTranslation()

  return (
    <Container id="certificates">
      <ScrollAnimation animateIn="fadeIn">
        <h2>{t('certificate')}</h2>
      </ScrollAnimation>

      <div className="certificates">
        {lists.map((item) => (
          <ScrollAnimation animateIn="flipInX">
            <div className="certificate">
              <header>
                <img
                  src="/assets/certificate.svg"
                  className="logoCertificate"
                  alt="certificate"
                />
                <div className="project-links">
                  <a
                    href={item.linkCertificate}
                    target="_blank"
                    rel="noreferrer"
                  >
                    <img
                      src="/assets/external-link-icon.svg"
                      alt="Visitar site"
                    />
                  </a>
                </div>
              </header>
              <div className="body">
                <h3>{item.title}</h3>
                <table>
                  <tbody>
                    <tr>
                      <td>Platforms:</td>
                      <td>
                        <a href={item.linkPlatforms} target="_blank">
                          {item.platforms}
                        </a>
                      </td>
                    </tr>
                    <tr>
                      <td>Finish On:</td>
                      <td>{convertToDate(item.finishedOn)}</td>
                    </tr>
                    <tr>
                      <td>Link Certificate:</td>
                      <td>
                        <a href={item.linkCertificate} target="_blank">
                          Link
                        </a>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <footer>
                <ul className="tech-list">
                  {item.tech.map((tech) => (
                    <li>{tech}</li>
                  ))}
                </ul>
              </footer>
            </div>
          </ScrollAnimation>
        ))}
      </div>
    </Container>
  )
}
