import React from 'react'
import { Container } from './styles'
import ScrollAnimation from 'react-animate-on-scroll'
import { lists } from '../../config/skills'
import { useTranslation } from 'gatsby-plugin-react-i18next'

export function Skills() {
  const { t } = useTranslation()

  return (
    <Container id="skills">
      <ScrollAnimation animateIn="fadeIn">
        <h2>{t('skill')}</h2>
      </ScrollAnimation>
      
      <div className="skills">
        <ScrollAnimation animateIn="flipInX">
          <div className="skill">
            <div className="body">
              <h3>Front End</h3>
            </div>
            <div className="body">
              {lists.fe.map((item, i: number) => {
                if (i === lists.fe.length - 1) {
                  return ` ${item}`
                }

                return ` ${item},`
              })}
            </div>
          </div>
        </ScrollAnimation>

        <ScrollAnimation animateIn="flipInX">
          <div className="skill">
            <div className="body">
              <h3>Back End</h3>
            </div>
            <div className="body">
              {lists.be.map((item, i: number) => {
                if (i === lists.be.length - 1) {
                  return ` ${item}`
                }

                return ` ${item},`
              })}
            </div>
          </div>
        </ScrollAnimation>

        <ScrollAnimation animateIn="flipInX">
          <div className="skill">
            <div className="body">
              <h3>Devops</h3>
            </div>
            <div className="body">
              {lists.devops.map((item, i: number) => {
                if (i === lists.devops.length - 1) {
                  return ` ${item}`
                }

                return ` ${item},`
              })}
            </div>
          </div>
        </ScrollAnimation>
      </div>
    </Container>
  )
}
